.menu {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  font-size: 18px;
  -ms-flex-pack: center;
      justify-content: center;
  left: 10px;
  top: 10px;
  z-index: 1;
}
.menu svg {
  margin-right: 5px;
}

@media (max-width: 960px) {
  .menu > li > a > button > span > span {
    display: none;
  }
  .menu > li > button > span > span {
    display: none;
  }
}
@media (max-width: 660px) {
  .menu {
    width: 60px;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }
}

@media (max-width: 380px) {
  .menu {
    -ms-flex-pack: start;
        justify-content: flex-start;
    left: 0;
    top: 0;
  }
}





