.header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: start;
      align-items: flex-start;
  height: 170px;
  min-width: 320px;
}
.navigation {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-pack: justify;
      justify-content: space-between;
  z-index: 1;
}
@media (max-width: 880px) {
  .header {
    -ms-flex-pack: start;
        justify-content: flex-start;
  }
  .navigation {
    width: 100%;
    -ms-flex-pack: justify;
        justify-content: space-between;
  }
}
@media (max-width: 380px) {
  .header {
    height: 140px;
  }
}


